import React, { Component, Fragment } from 'react'
import { Button, Form, Input, Col, message } from 'antd'
import request from '@/utils/request'
import './batter.less'
const { Item } = Form
class batter extends Component {
  constructor(props) {
    super(props)
    this.state = {
      user0: '',
      user1: '',
      url0: '',
      url1: '',
      loading0: false,
      loading1: false,
      flag0: false,
      flag1: false,
    }
  }
  imgerror = (e) => {
    e.currentTarget.src = require('@/assets/img/user.png')
  }
  render() {
    const { user0, user1, url0, url1, flag0, flag1, loading0, loading1 } = this.state
    return (
      <div className="battle-content">
        <div className="title-block">Instructions</div>
        <div>
          {['Enter two Github', 'Battle', 'See the winner'].map((opt, i) => (
            <div key={i}>
              <div>{opt}</div>
              <div>
                <img src={require(`@/assets/img/${['user', 'fight', 'cup'][i]}.png`)} alt="" />
              </div>
            </div>
          ))}
        </div>
        <div className="title-block">Instructions</div>
        <section className="row submit-box">
          <Form>
          <Item className="clearfix">
            <Col span={12}>
              {!flag0 ? (
                <Fragment>
                  <Item label="Player One" name='user0' rules = {[
                        { required: true, message: '请输入' },
                        { whitespace: true, message: '不能为空字符！' }]}
                      >
                      <Input
                        autoComplete="off"
                        placeholder={'请输入'}
                        className="form-item-warp"
                        onChange={(v)=>{
                          this.setState({
                            user0: v.target.value
                          })
                        }}
                        onPressEnter={(e) => this.pressEnter(e.target.value, 0)}
                      />
                  </Item>
                  <Button onClick={()=>this.pressEnter(user0, 0)} disabled={!user0} loading={loading0} type='primary'>submit</Button>
                </Fragment>
              ) : (
                <div className='img-info'>
                  <img src={url0} alt='' onError={this.imgerror} />
                  <span>{user0}</span>
                  <span className='img-close' onClick={()=>{
                    this.setState({
                      flag0: false
                    })
                  }}>X</span>
                </div>
              )}
            </Col>
            <Col span={12}>
              {!flag1 ? (
                <Fragment>
                  <Item label="Player Two" name='user1' rules = {[
                        { required: true, message: '请输入' },
                        { whitespace: true, message: '不能为空字符！' }]}
                      >
                      <Input
                        autoComplete="off"
                        placeholder={'请输入'}
                        className="form-item-warp"
                        onChange={(v)=>{
                          this.setState({
                            user1: v.target.value
                          })
                        }}
                        onPressEnter={(e) => this.pressEnter(e.target.value, 1)}
                      />
                  </Item>
                  <Button onClick={()=>this.pressEnter(user1, 1)} disabled={!user1} loading={loading1} type='primary'>submit</Button>
                </Fragment>
              ) : (
                <div className='img-info'>
                  <img src={url1}  alt='' onError={this.imgerror} />
                  <span>{user1}</span>
                  <span className='img-close' onClick={()=>{
                    this.setState({
                      flag1: false
                    })
                  }}>X</span>
                </div>
              )}
            </Col>
          </Item>
          { flag1 && flag0 ? <Button onClick={this.battle} className='battle-btn' type='primary'>Battle</Button> :  null}
          </Form>
        </section>     
      </div>
    )
  }
  pressEnter = (text, type) => {
    this.setState({
      [`user${type}`]: text,
      [`loading${type}`]: true
    }, ()=>{
      this.getImg(text, type)
    })
  }
  getImg=(text, type)=>{
    request(`https://api.github.com/users/${text}`, 'get').then(res => {
        this.setState({
          [`url${type}`]: res.avatar_url,
          [`user${type}`]: text,
          [`loading${type}`]: false,
          [`flag${type}`]: true,
        })
      }).catch(err=>{
          this.setState({
            [`loading${type}`]: false,
          })
      })
  }
  battle=()=>{
    const { user0, user1 } = this.state
    const { props } = this.props
    props.history.push({ pathname: '/battle/result', search: `?player1=${user0}&player2=${user1}` });
  }
}

export default batter
