import React, { Component } from 'react'
import Battle from '@/component/batter.jsx'

export default class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    return (
      <div className="battle-page">
        <Battle props={this.props} />
      </div>
    )
  }
}
