import axios from 'axios'
import qs from 'qs'
import { message } from 'antd'
// import { LOGINOUT_CODES, BASE_URL } from '@/assets/js/enume'

// 延长超时时间60秒
axios.defaults.timeout = 10000
//重连次数
axios.defaults.retry = 4
//接口间隔时间(ms)
axios.defaults.retryDelay = 1000
//超时重连接口
axios.interceptors.response.use(undefined, function axiosRetryInterceptor(err) {
  if (err && err.message.includes('timeout')) {
    let config = err.config
    // If config does not exist or the retry option is not set, reject
    if (!config || !config.retry) { return Promise.reject(err) }

    // Set the variable for keeping track of the retry count
    config.__retryCount = config.__retryCount || 0

    // Check if we've maxed out the total number of retries
    if (config.__retryCount >= config.retry) {
      // Reject with the error
      return Promise.reject(err)
    }

    // Increase the retry count
    config.__retryCount = 1

    // Create new promise to handle exponential backoff
    const backoff = new Promise(function (resolve) {
      setTimeout(function () {
        resolve()
      }, config.retryDelay || 1)
    })

    // Return the promise in which recalls axios to retry the request
    return backoff.then(function () {
      return axios(config)
    })
  } else {
    let res = err.response
    checkStatus(res)
  }
})

function checkStatus(res) {
  // 判断后台有响应但没有返回超时的情况表现为没有code
  //   const loginoutCodes = Object.keys(LOGINOUT_CODES).map(key => LOGINOUT_CODES[key])
  if (res.status >= 200 && res.status < 300) {
    if ((res.data.head || {}).code === '200') {
      return res
    }
    // else if (loginoutCodes.indexOf((res.data.head || {}).code) !== -1) {
    //   sessionStorage.clear()
    //   window.location.href = '/#/login'  //登录有了开
    // }
    else {
      return '请求超时，请稍后重试'
    }
  }
  res.response = res
  throw res
}

function catchCB(err) {
  let msg = '异常错误'
  if (err && err.message === 'Failed to fetch') {
    msg = '网络异常，请检查网络连接'
  } else if (err && err.response && err.response.status === 404) {
    msg = err.response.data.message
  } else if (err && err.response && err.response.status === 401) {
    msg = err.response.data.head.result
    sessionStorage.clear()
  } else if (err && err.response) {
    if (typeof err.response.data === 'string') {
      msg = '请求失败'
    } else if (typeof err.response.data.body === 'string') {
      if (!err.response.data.body) {
        msg = `${err.response.data.head.result}`
      } else {
        msg = `${err.response.data.body}`
      }
    } else if (err.response.data.body && typeof err.response.data.body === 'object') {
      msg = `${err.response.data.body.data || err.response.data.body.msg || err.response.data.head.result}`
    } else if (err.response.data.head) {
      msg = `${err.response.data.head.result}`
    } else {
      msg = err.response.data.message
    }
  } else if (err && err.message.includes('timeout')) {
    msg = '请求超时，请稍后重试'
  }
  return msg
}

function errPrompt(msg) {
  message.destroy()
  message.error(msg)
}

/**
 * Requests a URL, returning a promise.
 *
 * @param  {string} method    The type we want to request
 * @param  {string} url       The URL we want to request
 * @param  {object} data      The options we want to pass to "fetch"
 * @return {object}           An object containing either "data" or "err"
 */
const fetch = (url, data = {}, method) => {
  url = `${url}`

  //   const headers = createHeaders(data)
  // console.log(headers)
  // 当请求为非上传类型时，设置Content-Type
  const headers = {}
  if (!(data instanceof FormData)) {
    headers['Content-Type'] = 'application/json; charset=UTF-8'
  }

  // 处理参数
  if (method === 'post') {
    if (!(data instanceof FormData)) {
      // post请求设置参数
      // data = JSON.stringify(data)
    }
    // 发起请求
    return new Promise((resolve, reject) => {
      axios({
        method: method,
        url,
        data: data,
        headers: headers,
      })
        // .then(checkStatus)
        .then((res) => {
          resolve(res.data)
        })
        .catch((err) => {
          const msg = catchCB(err)
          errPrompt(msg)
          reject(err)
        })
    })
  } else {
    // get请求设置参数
    // url = data ? `${url}?${qs.stringify(data)}` : url
    // 发起请求
    return new Promise((resolve, reject) => {
      axios
        .get(url, {
          headers: headers,
        })
        // .then(checkStatus)
        .then((res) => {
          res.body = res.data
          resolve(res.data)
        })
        .catch((err) => {
          const msg = catchCB(err)
          errPrompt(msg)
          reject(err)
        })
    })
  }
}
export default fetch
